.screen-icons {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 500px;
	max-height: 830px;
	height: 100vh;
	box-sizing: border-box;
	padding: 0 15px;
	text-align: center;
	@include image('../img/screen-icons-bg.jpg');
	background-size: 100% 100%;
	@include r(992) {
		min-height: 400px; }
	@include r(600) {
		padding: 50px 15px;
		height: auto; }
	&-text {
		width: 910px;
		@include fs(24);
		@include lh(24, 30);
		@include r(1366) {
			width: 75%; }
		@include r(992) {
			@include fs(20); }
		@include r(600) {
			width: 100%;
			@include fs(18); }
		p {
			margin: 0; } } }
