.screen-text {
	margin: 0 auto;
	padding: 0 15px 60px;
	box-sizing: border-box;
	width: 760px;
	text-align: center;
	@include r(1100) {
		width: 560px; }
	@include r(600) {
		width: 80%; }
	&__title {
		margin: 0;
		@include fs(106);
		@include lh(106, 142);
		font-weight: 200;
		@include r(1400) {
			@include fs(96); }
		@include r(1100) {
			@include fs(76); }
		@include r(992) {
			@include fs(56); }
		@include r(600) {
			@include fs(46); }
		@include r(500) {
			@include fs(36); } }
	&__text {
		@include fs(50);
		@include lh(50, 51);
		font-weight: 300;
		color: $gray-dark;
		@include r(1400) {
			@include fs(45); }
		@include r(1100) {
			@include fs(35); }
		@include r(1100) {
			@include fs(30); }
		p {
			margin: 0; } } }
